import React, { Fragment } from 'react';
import classes from './Header.module.css';
import image1 from '../../static/images/image1.jpg';
import { Button, SvgButton } from '@mui/material';

const Header = () => {
    return (
        <Fragment>
        <div className={classes.header}> 
            <h1>It's All About Lines.</h1>
            <p>For the ones who see throught the lines</p>
            <a className={classes['button_learn_more']} href="#ABOUT" >Learn More</a>
        </div>      
        </Fragment>
    )
}

export default Header;
